/* App.css */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
}

nav {
  background-color: #333;
  color: white;
  padding: 10px 20px;
  text-align: center;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li a {
  text-decoration: none;
  color: white;
  font-size: 18px;
}

nav ul li a:hover {
  text-decoration: underline;
}

h1 {
  color: #333;
}

/* Additional styles in App.css */
.home-page {
  padding: 20px;
  text-align: center;
}

.home-page h1 {
  margin-bottom: 20px;
}

.home-page p {
  font-size: 16px;
  color: #666;
  line-height: 1.6;
}

/* More styles in App.css */
.projects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.project-item {
  border: 1px solid #ddd;
  margin: 10px;
  padding: 10px;
  width: 300px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.project-item h3 {
  color: #333;
}

.project-item p {
  font-size: 14px;
  color: #555;
}

.project-item img {
  width: 100%;
  height: auto;
}

.project-item a {
  color: #0066cc;
  text-decoration: none;
}
